@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);

*{

  margin: 0;
  padding: 0;

}
.Profile{

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 5rem;
    font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
    color: #999;
}

.img-wrapper {

    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-flow: row-reverse;

}

.img-wrapper img{
    width: 50%;
    border-radius: 50%;
}


.profile-data h1{

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 2.5rem;
    

}




.profile-data-content{

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}


.profile-repo h1{

    margin-bottom: 2.5rem;
    

}

a{

    text-decoration: none;
    text-transform: uppercase;
    color: #999;
    transition: all 0.5s;
   

}

a:hover{

    color: #6E48AA;
   

}


.Header-wrapper{

    background-image: url('https://wallpaperaccess.com/full/3415007.jpg');
    background-size: cover;
    -webkit-animation: zoom 10s;
            animation: zoom 10s;
}

@-webkit-keyframes zoom {

    0%{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
     
 }

@keyframes zoom {

    0%{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
     
 }

.Header{
    width: 100%;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-family: 'Roboto';
    color: #bbb;
}


.Header a{

    height: 5rem;
    width: 20rem;
    display: flex;
    margin-top: 1rem

}

.Header a img {
    width: 100%;
    height: 100%;
    
}


input{

    width: 11rem;
    height: 2rem;
    margin-bottom: 0.5rem;
    margin-top: 2rem;
    background-color: white;
    background-image: url('https://img.icons8.com/material-rounded/24/000000/search.png');
    background-position: 5px 5px;
    background-repeat: no-repeat;
    padding-left: 2.2rem;
    border: none;
    border-radius: 0.5rem;

}

button{
    color: rgba(255, 255, 255, 1);
    font-size: 1rem;
    background: #5b50bb;
    padding: 1rem 4.2rem;
    border: none;
    border-radius: 5px;
    text-transform: uppercase;
    letter-spacing: .1em;
    cursor: pointer;
}
